html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  font-family: "Montserrat", "Alumni Sans", sans-serif;
  color: #2d383f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #2d383f;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.container {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 5.86%;
}
@media (max-width: 767px) {
  .container {
    padding: 0 4.69%;
  }
}

.title-second,
h2 {
  color: #2d383f;
  font-family: "Montserrat", "Alumni Sans", sans-serif;
  font-size: 35px;
  font-style: normal;
  font-weight: 800;
  line-height: 35px;
  margin: 0;
}
@media (max-width: 1199px) {
  .title-second,
h2 {
    font-size: 25px;
    line-height: 25px;
  }
}
@media (max-width: 767px) {
  .title-second,
h2 {
    font-size: 22px;
    line-height: 22px;
  }
}

.btn {
  display: inline-flex;
  padding: 21px;
  width: 220px;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  cursor: pointer;
  border: none;
  background-color: #0266c1;
  user-select: none;
  box-sizing: border-box;
}

.main-header {
  position: relative;
}

.main-header__wrapper {
  position: relative;
  padding: 40px 0 80px;
  display: flex;
  flex-direction: column;
  gap: 77px;
}

.main-header__swiper-hero {
  position: absolute;
  inset: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #0266c1;
}

.main-header__swiper-hero img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-top__navigation ul {
  display: flex;
  justify-content: space-between;
  gap: 31px;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.header-top__navigation a {
  line-height: 16px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header-top__navigation a {
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.64px;
    text-transform: uppercase;
    color: #2d383f;
  }
}
@media (max-width: 1199px) and (max-width: 767px) {
  .header-top__navigation a {
    font-size: 14px;
    line-height: 14px;
    text-transform: uppercase;
  }
}

.header-top__telephone a {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header-top__telephone a {
    font-size: 16px;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .header-top__telephone a {
    font-size: 14px;
    line-height: 14px;
  }
}

.video-hero-container {
  display: inline-block;
  position: relative;
  width: 482px;
  height: 317px;
}

.video-hero-container video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  user-select: none;
}

.video-hero-container__button {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 0;
  padding: 0;
  background-color: transparent;
  user-select: none;
}

.video-hero-container__button-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  border-radius: 50%;
}

.header-bottom {
  max-width: 100%;
}

.header-bottom__container {
  position: relative;
}

.header-bottom h1 {
  position: relative;
  z-index: 1;
  margin-top: 0;
  margin-bottom: 22px;
  max-width: 770px;
  font-size: 77px;
  font-weight: 800;
  line-height: 77px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header-bottom h1 {
    font-size: 56px;
    line-height: 56px;
  }
}
@media (max-width: 767px) {
  .header-bottom h1 {
    font-size: 30px;
    line-height: 30px;
  }
}

.header-bottom p {
  margin-top: 0;
  margin-bottom: 37px;
  max-width: 425px;
  font-size: 17px;
  line-height: 24px;
  color: #ffffff;
}
@media (max-width: 767px) {
  .header-bottom p {
    font-size: 15px;
    line-height: 20px;
  }
}

.header-bottom__button {
  margin-bottom: 150px;
}

.header-bottom__descriptions-container {
  display: flex;
  gap: 10px;
}

.header-bottom__description-container {
  padding: 14px 20px;
  display: flex;
  gap: 8px;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.header-bottom__description-container p {
  margin: 0;
  font-size: 12px;
  line-height: 16px;
  color: #ffffff;
}

.header-bottom__video-contaner {
  position: absolute;
  right: 0;
  top: 119px;
}

.wrapper {
  position: relative;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.is-open {
  display: inline-block;
}

.is-close {
  display: none;
}

